*{
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.App{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.Container{
  width: 880px;
  height: 100vh;
}

header{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1{
  margin-top: 2.5%;
}

h2{
  margin-top: 2.5%;
  margin-bottom: 2.5%;
}

nav, nav ul{
  width: 100%;
  height: auto;
}

nav ul{
  display: flex;
  justify-content: center;
  gap:1%;
}

li{
  list-style-type: none;
}

a{
  text-decoration: none;
}

.PracticeContainer, .ChartsContainer, .KeyboardContainer, .QuizRowContainer{
  width: 100%;
  height: 500px;
  margin-top:2.5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 18px;
}

.QuizTestContainer{
  width: 100%;
  height: auto;
  text-align: center;
}

.QuizTest{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  font-size: 18px;
  gap:3%;
}

.QuestionContainer{
  width: 60%;
  margin-top: 2.5%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.Card{
  margin:1px;
}

.Character{
  font-size: 36px;
  text-align: center;
}

.CharacterInput{
  width: 120px;
  height: auto;
  border: none;
  border-bottom: 1px black solid;
  outline: none;
  font-size: 36px;
  text-align: center;
}

.ButtonContainer{
  padding-bottom:5%;
  margin-top: 5%;
  display: flex;
  gap:5%;
}

button{
  width: 110px;
  height: 40px;
  color: white;
  background-color: #008CBA;
  border: none;
  border-radius: .25rem;
  font-size: 16px;
}

.Button2{
  background-color: #6c757d;
}

.ChartHeader2{
  margin-top: 5%;
}

.Chart{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}

.KeyContainer{
  width: 100%;
  height: 100%;
}

.KeyContainer button{
  color: black;
  background-color: white;
  border:none;
}

table{
  width: 100%;
  height:100%;
  border-collapse: collapse;
  text-align: center;
}

table, tr, td{
  border: 1px solid black;
}

td{
  padding: 5px;
  cursor: pointer;
}

td button{
  width: 65px;
  height: 25px;
}

@media only screen and (max-width: 350px) {
  h1, h2{
    font-size: 22px;
  }
  .QuizTest{
    flex-direction: column;
  }
  .ButtonContainer{
    padding-bottom: 10%;
    margin-top: 10%;
  }
  .Chart{
    display: block;
    text-align: center;
  }
}